import { ajax } from 'rxjs/ajax';
import {
  map,
  startWith,
  switchMap,
  flatMap,
  catchError,
  filter,
  share, take, tap,
} from 'rxjs/operators';
import { of } from 'rxjs';
import { combineToArray } from './helpers';

export const organizations$ = ajax(
  `https://services.arcgis.com/WLhB60Nqwp4NnHz3/arcgis/rest/services/idaho_open_data_endpoints/FeatureServer/0/query?where=1%3D1&objectIds=&time=&resultType=none&outFields=*&returnIdsOnly=false&returnUniqueIdsOnly=false&returnCountOnly=false&returnDistinctValues=false&orderByFields=&groupByFieldsForStatistics=&outStatistics=&having=&resultOffset=&resultRecordCount=&sqlFormat=none&f=pjson`)
  .pipe(
    switchMap((data) => {
      return data.response.features.map(
        (feature => feature.attributes.ORG_ENDPOINT));
    }),
    flatMap((url) => {
      return ajax(url);
    }),
    map((data) => {
      return ({
        id: data.response.id,
        name: data.response.name,
        url: data.request.url,
      });
    }),
    startWith([]),
    catchError((error) => {
      return of([]);
    }),
    map((org) => {
      const query = `orgid:${org.id}`;
      return {
        ...org,
        url: `https://www.arcgis.com/sharing/rest/community/groups?f=pjson&q=isopendata:true AND (${query})&num=100`,
      };
    }),
    flatMap((org) => {
      return ajax(org.url).pipe(
        map((data) => {
          return {
            ...org,
            groups: data.response.results.map((result) => ({
              id: result.id,
              title: result.title,
              snippet: result.snippet,
              owner: result.owner,
            })),
          };
        }),
      );
    }),
    filter(org => org.groups.length > 0),
  ).pipe(
    combineToArray(),
    share(),
  );

